import "./ContactListAdmin.css";
import { DataGrid } from "@mui/x-data-grid";
import { DeleteOutline } from "@mui/icons-material";
import { Link } from "react-router-dom";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { deleteContact, getContacts } from "../../../redux/contact/apiCalls";
import { JsConst } from "../../../constant/JsConst";
import { DUMMY_USER_URL } from "../../../config/appConfig";
import { constContact } from "../../../model/contact/constContact";
import { useState } from "react";
import { contactTransactionService } from "../../../services/contactTransaction";
import { constTranslate } from "../../../constant/translate";
import { Modal } from "../../../components/Modal";
import { showErrorMsg } from "../../../services/eventBusService";
import { userService } from "../../../services/user.service";
import { constUser } from "../../../model/user/constUser";
import { utilService } from "../../../services/util.service";
import { ComponentLoader } from "../../../components/Loader/ComponentLoader";

export default function ContactListAdmin() {
    const dispatch = useDispatch();
    const { contacts } = useSelector((state) => state.contacts);
    const { currentUser } = useSelector((state) => state.user);

    const [filteredContacts, setFilteredContacts] = useState(contacts || [])
    const [modalProps, setModalProps] = useState(null)

    useEffect(() => {
        if (!contacts.length) getContacts(dispatch)
    }, [])

    useEffect(() => {
        if (contacts.length) setFilteredContacts(contacts)
    }, [contacts])

    const handleDelete = (id) => {
        const res = window.confirm('Are you sure you want to delete?')
        if (res) deleteContact(dispatch, id, currentUser);
    }

    const columns = [
        {
            field: "company",
            headerName: "Company",
            width: 200,
            renderCell: (params) => {
                return (
                    <div className="contactListItem">
                        <img className="contactListImg" src={params.row?.img?.url || DUMMY_USER_URL} alt="" />
                        {params.row.company}
                    </div>
                );
            },
        },
        { field: JsConst.CATEGORY, headerName: "Category", width: 220 },
        { field: JsConst.JOBTITLE, headerName: "Job Title", width: 220 },
        { field: JsConst.NAME, headerName: "Name", width: 220 },
        { field: JsConst.FAMILY_NAME, headerName: "Family Name", width: 220 },
        {
            field: JsConst.PRICE,
            headerName: "Price",
            width: 160,
            renderCell: (params) => {
                return (
                    <div className="contactListItem">
                        {params.row.price}$
                    </div>
                );
            },
        },
        {
            field: JsConst.BUYER,
            headerName: constTranslate.BUYERS,
            width: 160,
            renderCell: (params) => {
                return (
                    <div className="contactListItem">
                        {
                            params.row.transactionHistory?.length ?
                                <span className="link" onClick={() => setModalProps(params.row)}>
                                    {params.row?.transactionHistory.length} {constTranslate.BUYERS}
                                </span>
                                :
                                <span>{constTranslate.NO_BUYERS}</span>
                        }
                    </div>
                );
            },
        },
        {
            field: "action",
            headerName: "Action",
            width: 150,
            renderCell: (params) => {
                return (
                    <>
                        <Link to={"/admin/contact/" + params.row._id}>
                            <button className="contactListEdit">Edit</button>
                        </Link>
                        <DeleteOutline
                            className="contactListDelete"
                            onClick={() => handleDelete(params.row._id)}
                        />
                    </>
                );
            },
        },
    ]

    const getFilterContacts = (filterBy) => {
        switch (filterBy) {
            case 'notInStock':
                const notInStockContacts = contacts.filter(contact => !contact[constContact.IN_STOCK])
                return setFilteredContacts(notInStockContacts)
            case 'inStock':
                const inStockContacts = contacts.filter(contact => contact[constContact.IN_STOCK])
                return setFilteredContacts(inStockContacts)
            default:
                return setFilteredContacts(contacts)
        }
    }

    const getOrderContact = async () => {
        const res = await contactTransactionService.query()
        let transactions = res.filter(transaction => {
            return !transaction.contact.inStock
        })
        transactions = transactions.map(transaction => {
            return transaction.contact
        })
        const trans = contactTransactionService.removeDuplicateObjects(transactions)
        setFilteredContacts(trans)
    }


    return (
        <div className="contactList">
            <div className="btn-container">
                <button className="btn primary-btn" onClick={() => getFilterContacts('notInStock')}>Not in stock</button>
                <button className="btn primary-btn" onClick={() => getFilterContacts('inStock')}>In stock</button>
                <button className="btn primary-btn" onClick={() => getOrderContact()}>Order not in stock</button>
                <button className="btn primary-btn" onClick={() => getFilterContacts(null)}>All</button>
            </div>

            <DataGrid
                rows={filteredContacts}
                disableSelectionOnClick
                columns={columns}
                getRowId={(row) => row._id}
                pageSize={10}
                checkboxSelection
            />
            <div className="table-create-btn">
                <Link to="/admin/new-contact">
                    <button className="contactAddButton">Create</button>
                </Link>
            </div>

            {
                modalProps && <Modal setModalProps={setModalProps} >
                    <ContactTransactionList modalProps={modalProps} />
                </Modal>
            }
        </div>
    )
}

const ContactTransactionList = ({ modalProps }) => {
    const [isLoading, setIsLoading] = useState(false)
    const [selectedUsers, setSelectedUsers] = useState([])

    useEffect(() => {
        loadUsers()
    }, [])

    const loadUsers = async () => {
        setIsLoading(true)
        try {
            const selectedUsers = await userService.getUsers({ transactionContactId: modalProps._id })
            console.log('selectedUsers', selectedUsers)
            setSelectedUsers(selectedUsers)
        } catch (err) {
            showErrorMsg('Cannot get buyers list')
        } finally {
            setIsLoading(false)
        }
    }

    const columns = [
        {
            field: "user",
            headerName: constTranslate.USERNAME,
            width: 200,
            renderCell: (params) => {
                return (
                    <div className="userListUser">
                        <img className="userListImg" src={params.row?.imgUrl?.url || DUMMY_USER_URL} alt="user" />
                        {params.row[constUser.FULLNAME]}
                    </div>
                );
            },
        },
        {
            field: "date",
            headerName: constTranslate.PURCHASE_DATE,
            width: 160,
            renderCell: (params) => {
                const purchaseDate = getPurchaseDate(params.row)
                return (
                    <div className="userListUser">
                        {
                            purchaseDate ?
                                utilService.getFormattedDate(new Date(purchaseDate))
                                :
                                constTranslate.NO_DATE
                        }
                    </div>
                );
            },
        },
    ];

    const getPurchaseDate = (user) => {
        if (!user) return false
        const transaction = user[constUser.CONTACT_TRANSACTIONS]?.find(trans => trans.userId === user._id)
        if (transaction) return transaction.createdAt
        else return false
    }

    return (
        <div className="contact-transaction-modal">
            {!isLoading && <DataGrid
                rows={selectedUsers}
                disableSelectionOnClick
                columns={columns}
                getRowId={(row) => row._id}
                pageSize={10}
            />}
            <ComponentLoader isLoading={isLoading} />
        </div>
    )
}